<template>
  <div class="goods">
    <!-- 添加商品 -->
    <el-button @click="goodsAdd" type="success" plain>添加商品</el-button>

    <!-- 表格 -->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="index" :index="indexMethod" width="50">
      </el-table-column>
      <el-table-column prop="goods_name" label="商品名称"></el-table-column>
      <el-table-column prop="goods_price" label="商品价格"></el-table-column>
      <el-table-column prop="goods_weight" label="商品重量"></el-table-column>
      <el-table-column prop="upd_time" label="创建时间">
        <template v-slot:default="{row}">
          {{row.add_time | time}}
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template v-slot:default="{row}">
          <el-button size="small" @click="showAlterVisible(row)" type="primary" icon="el-icon-edit" plain></el-button>
          <el-button size="small" @click="delGoods(row)" type="danger" icon="el-icon-delete" plain></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="pagenum"
    :page-sizes="[2, 5, 6, 8]"
    :page-size="pagesize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="total">
    </el-pagination>

    <!-- 修改对话框 -->
    <el-dialog
    title="提示"
    :visible.sync="alterVisible"
    width="40%">

      <el-form label-width="80px">
        <el-form-item label="商品名称">
            <el-input v-model="alterForm.goods_name"></el-input>
        </el-form-item>

        <el-form-item label="商品价格">
            <el-input v-model="alterForm.goods_price"></el-input>
        </el-form-item>

        <el-form-item label="商品数量">
            <el-input v-model="alterForm.goods_number"></el-input>
        </el-form-item>

        <el-form-item label="商品重量">
            <el-input v-model="alterForm.goods_weight"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <template v-solt:default="{row}">
          <el-button @click="alterVisible = false">取 消</el-button>
          <el-button type="primary" @click="alterCatgories">确 定</el-button>
        </template>

      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [],
      pagenum: 1,
      pagesize: 5,
      total: 0,
      alterVisible: false,
      alterForm: {
        id: null,
        goods_name: '',
        goods_price: '',
        goods_number: '',
        goods_weight: ''
      }

    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      const { data, meta } = await this.$axios.get('goods', {
        params: {
          pagenum: this.pagenum,
          pagesize: this.pagesize
        }
      })
      if (meta.status === 200) {
        // console.log(data)
        this.total = data.total
        this.tableData = data.goods
      }
    },
    // 分页
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pagesize = val
      this.pagenum = 1
      this.getGoodsList()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.pagenum = val
      this.getGoodsList()
    },
    // 跳转添加
    goodsAdd () {
      this.$router.push('/goods-add')
    },
    // 修改
    showAlterVisible (row) {
      this.alterVisible = true
      console.log(row)
      this.alterForm.id = row.goods_id
      this.alterForm.goods_name = row.goods_name
      this.alterForm.goods_price = row.goods_price
      this.alterForm.goods_number = row.goods_number
      this.alterForm.goods_weight = row.goods_weight
    },
    async alterCatgories () {
      const { meta } = await this.$axios.put(`goods/${this.alterForm.id}`, {
        goods_name: this.alterForm.goods_name,
        goods_price: this.alterForm.goods_price,
        goods_number: this.alterForm.goods_number,
        goods_weight: this.alterForm.goods_weight
      })
      if (meta.status === 200) {
        this.alterVisible = false
        this.$message.success(meta.msg)
        this.getGoodsList()
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 删除
    async delGoods (row) {
      try {
        await this.$confirm('你确定要删除吗, 是否继续', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        const { meta } = await this.$axios.delete(`goods/${row.goods_id}`)
        if (meta.status === 200) {
          this.$message.success(meta.msg)
          this.getGoodsList()
        } else {
          this.$message.error(meta.msg)
        }
      } catch (e) {
        console.log(e)
      }
    }
  },
  computed: {
    // 排序
    indexMethod () {
      return (this.pagenum - 1) * this.pagesize + 1
    }

  }

}
</script>

<style lang="scss" scoped>
  .el-button{
    margin-bottom: 10px;
  }
  .el-table{
    margin-bottom: 10px;
  }
</style>
