<template>
  <div class="categories">
    <!-- 添加按钮 -->
    <el-button @click="showAddVisible" type="success" plain>添加分类</el-button>

    <!-- 表格 -->
    <el-table
    :data="catagoriesList"
    v-loading="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    row-key="cat_id">

      <el-table-column prop="cat_name" label="分类名称"></el-table-column>

      <el-table-column label="是否有效">
        <template v-slot:default="{row}">
          {{row.cat_deleted? "否": '是'}}
        </template>
      </el-table-column>

      <el-table-column prop="cat_level" label="排序"></el-table-column>

      <el-table-column label="操作">
        <template v-slot:default="{row}">
          <el-button size="small" @click="showAlterVisible(row)" type="primary" icon="el-icon-edit" plain></el-button>
          <el-button size="small" @click="deleteCat(row)" type="danger" icon="el-icon-delete" plain></el-button>
        </template>

      </el-table-column>

    </el-table>

    <!-- 分页 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 5, 8]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>

    <!-- 添加对话框 -->
    <el-dialog
    title="提示"
    :close="closeDialog"
    :visible.sync="addVisible"
    width="40%">
      <!-- 表单 -->
      <el-form :model="form" :rules="rules" ref="form" label-width="80px">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="form.cat_name" placeholder="请输入分类名称"></el-input>
        </el-form-item>
        <el-form-item label="父级名称" prop="cat_pid">
          <el-cascader
          clearable
          v-model="form.cat_pid"
          :options="options"
          :props="props"></el-cascader>
        </el-form-item>

      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCatgories" >确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改对话框 -->
    <el-dialog
    title="修改分类名称"
    :visible.sync="alterVisible"
    width="40%">
      <el-form label-width="80px">
        <el-form-item label="分类名称">
            <el-input v-model="alterForm.cat_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="alterVisible = false">取 消</el-button>
        <el-button type="primary" @click="alterCat">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      alterForm: {
        cat_name: '',
        id: null
      },
      alterVisible: false,
      loading: true,
      pagenum: 1,
      pagesize: 5,
      total: 0,
      catagoriesList: [],
      addVisible: false,
      form: {
        cat_pid: [],
        cat_name: '',
        cat_level: ''
      },
      rules: {
        cat_name: [
          { required: true, message: '请输入活动名称', trigger: ['blur', 'change'] },
          { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: ['blur', 'change'] }
        ]
      },
      options: [],
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      }
    }
  },
  created () {
    this.getCategoriesList()
  },
  methods: {
    async getCategoriesList () {
      this.loading = true
      const { data, meta } = await this.$axios.get('categories', {
        params: {
          type: 3,
          pagenum: this.pagenum,
          pagesize: this.pagesize
        }
      })
      if (meta.status === 200) {
        // console.log(data)
        this.total = data.total
        this.catagoriesList = data.result
      }
      this.loading = false
    },
    // 分页
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pagesize = val
      this.pagenum = 1
      this.getCategoriesList()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.pagenum = val
      this.getCategoriesList()
    },
    // 添加
    async showAddVisible () {
      this.addVisible = true
      const { data, meta } = await this.$axios.get('categories?type=2')
      if (meta.status === 200) {
        this.options = data
      }
    },
    async addCatgories () {
      this.addVisible = false
      try {
        await this.$refs.form.validate()
        const { meta } = await this.$axios.post('categories', {
          cat_pid: this.form.cat_pid[this.form.cat_pid.length - 1] || 0,
          cat_name: this.form.cat_name,
          cat_level: this.form.cat_pid.length
        })
        if (meta.status === 201) {
          this.$message.success(meta.msg)
          this.getCategoriesList()
        } else {
          this.$message.error(meta.msg)
        }
      } catch (e) {
        console.log(e)
      }
    },
    closeDialog () {
      this.$refs.form.resetFields()
    },
    // 删除分类
    async deleteCat (row) {
      // console.log(row)
      try {
        await this.$confirm('你确定要删除吗, 是否继续?', '温情提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        const { meta } = await this.$axios.delete(`categories/${row.cat_id}`)
        if (meta.status === 200) {
          this.$message.success(meta.msg)
          this.getCategoriesList()
        } else {
          this.$message.error(meta.msg)
        }
      } catch (e) {
        console.log(e)
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      }
    },
    // 修改
    showAlterVisible (row) {
      this.alterVisible = true
      this.alterForm.cat_name = row.cat_name
      this.alterForm.id = row.cat_id
      console.log(row)
    },
    async alterCat () {
      // console.log(this.alterForm.id)
      const { meta } = await this.$axios.put(`categories/${this.alterForm.id}`, {
        cat_name: this.alterForm.cat_name
      })
      if (meta.status === 200) {
        this.alterVisible = false
        this.$message.success(meta.msg)
        this.getCategoriesList()
      } else {
        this.$message.error(meta.msg)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-button{
    margin-bottom: 10px;
  }
  .el-table{
    margin-bottom: 10px;
  }
</style>
