<template>
  <div class="goods-add">
    <!-- 步骤条 -->
    <el-steps :active="active" finish-status="success">
      <el-step title="步骤 1" description="基本信息"></el-step>
      <el-step title="步骤 2" description="商品图片"></el-step>
      <el-step title="步骤 3" description="商品内容"></el-step>
    </el-steps>

    <!-- 标签页 -->
    <el-tabs v-model="activeTab" @tab-click="handleClick" tab-position="left">
      <el-tab-pane label="基本信息">
        <el-form ref="form" label-width="80px" :model="form">

          <el-form-item label="商品名称">
            <el-input v-model="form.goods_name" placeholder="请输入商品名称"></el-input>
          </el-form-item>

          <el-form-item label="商品价格">
            <el-input v-model="form.goods_price" placeholder="请输入商品价格"></el-input>
          </el-form-item>

          <el-form-item label="商品重量">
            <el-input v-model="form.goods_weight" placeholder="请输入商品重量"></el-input>
          </el-form-item>

          <el-form-item label="商品数量">
            <el-input v-model="form.goods_number" placeholder="请输入商品数量"></el-input>
          </el-form-item>

          <el-form-item label="商品分类">
            <el-cascader
            v-model="form.goods_cat"
            :props="props"
            :options="options">
            </el-cascader>
          </el-form-item>

        </el-form>
        <el-button type="primary" style="margin-top: 12px;" @click="next">下一步</el-button>
      </el-tab-pane>

      <el-tab-pane name="1" label="商品图片">
        <el-upload
        multiple
        name="file"
        action="http://localhost:8888/api/private/v1/upload"
        list-type="picture-card"
        :headers="headers"
        :on-preview="handlePictureCardPreview"
        :on-success="handlesSuccess"
        :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
        <el-button type="primary" style="margin-top: 12px;" @click="next">下一步</el-button>
      </el-tab-pane>

      <el-tab-pane label="商品内容">
        <quill-editor
        v-model="form.goods_introduce">
        </quill-editor>
        <el-button type="primary" style="margin-top: 12px;" @click="addGoods">添加</el-button>
      </el-tab-pane>

    </el-tabs>

  </div>
</template>

<script>
export default {
  data () {
    return {
      active: 0,
      activeTab: '',
      form: {
        goods_name: '', // 商品名称
        goods_cat: '', // 分类列表
        goods_price: '', // 价格
        goods_number: '', // 数量
        goods_weight: '', // 重量
        goods_introduce: '',
        pics: [] // 图片数组
      },
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      options: [],
      dialogImageUrl: '',
      dialogVisible: false,
      headers: {
        Authorization: localStorage.getItem('token')
      }
    }
  },
  async created () {
    const { data, meta } = await this.$axios.get('categories?type=3')
    if (meta.status === 200) {
      this.options = data
    }
  },
  methods: {
    next () {
      // console.log(this.active)
      if (this.active++ > 2) this.active = 0
      this.activeTab = this.active + ''
    },
    handleClick (tab) {
      // console.log(tab)
      this.active = +tab.index
    },
    // 图片预览
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 删除图片
    handleRemove (file) {
      // console.log(file)
      this.form.pics = this.form.pics.filter(item => item.pic !== file.response.data.tmp_path)
    },
    // 图片上传成功的回调
    handlesSuccess (response, file) {
      // console.log(response, file)
      const { data, meta } = response
      if (meta.status === 200) {
        this.form.pics.push({
          pic: data.tmp_path
        })
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 添加
    async addGoods () {
      const { meta } = await this.$axios.post('goods', {
        ...this.form,
        goods_cat: this.form.goods_cat.join(',')
      })
      if (meta.status === 201) {
        this.$message.success(meta.msg)
        this.$router.push('/goods')
      } else {
        this.$message.error(meta.msg)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .quill-editor{
    background-color: #fff;
    ::v-deep .ql-editor{
      min-height: 400px;
    }
  }
</style>
